$red: rgb(204, 185, 118);

html {
    font-family:'Courier New', Courier, monospace;
    font-size: 16px;
}

body {
    height: 100vh;
    margin: 0;
    padding: 0;
    background: $red;
    * {
        border: 1px solid rgb(255, 255, 168);
        margin: 0;
    }
}

.container {
    height: calc(100vh - 100%);
    padding: 0rem 2rem;
}

.header {
    display: grid;
    grid-auto-flow: column;
    justify-content: space-between;
    align-items: center;
    height: 100px;
    .brand {
        display: grid;
        grid-auto-flow: column;
        align-items: center;
        gap: 1rem;
        .logo {
            .logo-img {
                width: 3rem;
                display: block;
            }
        }
        .name {
            font-size: 2rem;
            font-weight: 600;
        }
    }

    .nav {
        .nav-list {
            display: grid;
            grid-auto-flow: column;
            gap: 1.25rem;
            .nav-item {
                list-style-type: none;
                font-size: 1.25rem;
                font-weight: 500;
                .nav-link {
                    text-decoration: none;
                }
            }
        }
    }
}

.hero {
    display: grid;
    grid-auto-flow: column;
    align-items: center;
    border: 1px solid red;
    min-height: 400px;

    .hero-text {
        display: grid;
        grid-auto-flow: row;
        .hero-title {}
        .hero-description {}
    }
    .hero-img {
        width: 60vw;
    }
}